body {
  margin: 0;
  background-color: #F5F7FB;
  /* background-color: red; */
  box-sizing: border-box;
  position: relative;
  min-height: 100vh;
  font-family: 'Karla', sans-serif;
  font-weight: 500;
  color: #293264;
}

.quizzical {
  position: relative;
  padding: 5rem 0;
}

body::before {
  content: url('./assets/blob-upper-right.png');
  position: absolute;
  right: 0;
  top: 0;
}

body::after {
  content: url('./assets/blob-lower-left.png');
  position: absolute;
  left: 0;
  bottom: 0;
}

.quizzical-container {
  width: 80%;
  max-width: 600px;
  margin: 0 auto;
}

.question-container {
  border-bottom: 1px solid #DBDEF0;
  padding-bottom: 1.2rem;
}

.possible-answers-container {
  display: flex;
  gap: .8rem;
  flex-wrap: wrap;
}

.possible-answer {
  border: 1px solid #4D5B9E;
  border-radius: 10px;
  padding: .2em 1em;
  user-select: none;
}

.possible-answer.correct {
  background: limegreen;
}

.button-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-top: 2rem;
}

.button {
  background-color: #4D5B9E;
  border-radius: 10px;
  border: none;
  color: #F5F7FB;
  font-family: 'Karla', sans-serif;
  font-size: 1.3rem;
  padding: .7em 1.4em;
  transition: all .15s ease;
}

.button:hover {
  opacity: .9;
}

.button:active {
  transform: scale(.95);
}

@media only screen and (max-width: 700px) {
  .quizzical {
    padding: 2rem 0;
  }

  .quizzical-container {
    width: 90%;
  }

  .button-container {
    flex-direction: column;
  }
}
